body{
	background-color: rgb(250, 250, 250)
}

.container{
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
}

.image{
	width: 370px;
	margin-top: 30px;
	margin-bottom: 20px;
}

